<template>
  <div>
    <b-row class="mb-2">
      <b-col xl="1"></b-col>
      <b-col xl="3">
        <div class="title-fw title-fs">{{ $t("EXPENSEYM") }}</div>
      </b-col>
      <b-col>
        <div>{{ expenseYM }}</div>
      </b-col>
    </b-row>
    <b-row class="mb-2">
      <b-col xl="1"></b-col>
      <b-col xl="3">
        <div class="title-fw title-fs">{{ $t("JOURNALIMPORTFILE") }}</div>
      </b-col>
      <b-col>
        <div>
          {{ fileName }}
        </div>
      </b-col>
    </b-row>
    <b-row>
      <b-col xl="1"></b-col>
      <b-col xl="6">
        <div v-if="pointsMessage.length > 0" class="overflow-y h6 text-danger">
          <span
            v-for="(point, index) in pointsMessage"
            :key="index"
            class="text-danger"
            >{{ point }}<br
          /></span>
        </div>
      </b-col>
    </b-row>
    <b-row>
      <b-col xl="1"></b-col>
      <b-col xl="3">
        <div class="title-fw title-fs">{{ $t("ROWCOUNT") }}</div>
      </b-col>
      <b-col>
        <div class="mt-1">
          {{ tableData.length }}
        </div>
      </b-col>
    </b-row>

    <hr class="hrBarType" />
    <!-- 一覧内容 -->
    <b-table
      ref="table"
      :fields="fields"
      :items="tableData"
      :borderless="false"
      :outlined="true"
      :busy="isBusy"
      :striped="true"
      head-variant="dark"
      sticky-header
      fixed
      responsive="sm"
      small="small"
    >
      <template #table-colgroup="scope">
        <col
          v-for="field in scope.fields"
          :key="field.key"
          :style="{
            width: field.key === 'filterElements' ? '135px' : '58px',
          }"
        />
      </template>
      <template #head(filterElements)="item">
        <div class="d-flex text-right">
          <b-form-checkbox
            v-model="allSelected"
            :name="'checkbox-All' + item"
          ></b-form-checkbox>
          <span>{{ $t("BINDINGALREADYNOTSHOW") }}</span>
        </div>
      </template>
      <template v-slot:table-busy>
        <div class="text-center text-danger my-2">
          <b-spinner class="align-middle"></b-spinner>
        </div>
      </template>
      <template v-slot:cell(selectRow)="item">
        <b-form-checkbox
          :id="'checkbox-' + item.index"
          v-model="item.item.selectRow"
          :name="'checkbox-' + item.index"
          :ref="'checkbox' + item.index"
          :checked="item.item.selectRow"
          @change="toggleSelectAll(item)"
        ></b-form-checkbox>
      </template>
      <template v-slot:cell(cost_monthly)="item">{{ item.value }}</template>
      <template v-slot:cell(deal_date)="item">{{ item.value }}</template>
      <template v-slot:cell(accounts_name_nm)="item">{{ item.value }}</template>
      <template v-slot:cell(cost)="item">{{ item.value }}</template>
      <template v-slot:cell(description)="item">{{ item.value }}</template>
      <template v-slot:cell(expense_item_nm)="item">{{ item.value }}</template>
    </b-table>
    <b-row class="d-flex justify-content-end mb-1">
      <b-button
        @click="showCostItemSelectModal"
        :disabled="!checkedItems.length > 0"
        variant="primary"
        class="mr-2"
        >{{ $t("EXPENSEITEMSELECTION") }}</b-button
      >
      <b-button
        @click="mapExpenseItem"
        :disabled="
          selected_expense_item.expense_item_cd == null ||
          !checkedItems.length > 0 ||
          loading
        "
        variant="primary"
        >{{ $t("BINDING") }} <i v-if="loading" class="fa fa-spinner fa-spin"
      /></b-button>
    </b-row>

    <ResizableModal
      :modalTitle="$t('OURNALIMPORTFILEEXPENSEITEMSELECTION')"
      :draggable="true"
      :showModal="showModal"
      @modal-submit="applyCheckExpItem"
      @modal-hide="hideModal"
    >
      <template slot="modal-content">
        <b-card class="pop-up-box">
          <b-row>
            <b-col class="submenu-scroll">
              <SubMenuTree
                @update-expense-type="updateExpenseItem"
                :tree-options="treeOptions"
                :disabledItems="disabledExpenseItems"
                :treeTitle="addExpenseTitle"
              />
            </b-col>
          </b-row>
        </b-card>
      </template>
    </ResizableModal>

    <!-- 仕訳帳費目選択 -->
    <!-- <b-modal
      id="costItemSelect-modal"
      ref="costItemSelect-modal"
      :hide-footer="false"
      centered
      variant="secondary"
      modal-class="py-0"
      body-class="border-0 py-0"
      size="md"
      footer-class="border-0"
      header-class="border-0 bg-white p-1"
      button-size="sm"
      hide-header-close
    >
      <template v-slot:modal-header="{ cancel }">
        <div class="title-fw title-fs text-dark ml-2">
          {{ $t("OURNALIMPORTFILEEXPENSEITEMSELECTION") }}
        </div>
        <div class="d-flex justify-content-end">
          <b-button
            @click="cancel()"
            variant="primary"
            class="back-btn close-btn mt-2"
          >
            {{ $t("CLOSE") }}
          </b-button>
        </div>
      </template>
      <b-card class="pop-up-box">
        <b-row>
          <b-col>
            <SubMenuTree
              @update-expense-type="updateExpenseItem"
              :tree-options="treeOptions"
              :disabledItems="disabledExpenseItems"
              :treeTitle="addExpenseTitle"
            />
          </b-col>
        </b-row>
      </b-card>
      <template #modal-footer>
        <div class="w-100">
          <b-button
            @click="applyCheckExpItem"
            variant="primary"
            type="submit"
            class="px-4 send-btn float-right"
            >{{ $t("SELECTEXPENSEITEMS") }}</b-button
          >
        </div>
      </template>
    </b-modal> -->
  </div>
</template>

<script>
import api from "@/shared/api";
import { Settings } from "luxon";
Settings.defaultLocale = "ja";
import { ApiServicesFactory } from "@/shared/apiServiceFactory";
const BasicInfoApis = ApiServicesFactory.get("basic");
import SubMenuTree from "@/components/SubMenuTree.vue";
import ResizableModal from "@/components/modals/ResizableModal";

import { mapGetters } from "vuex";
import { endPoint } from "@/shared/endPoint";
import moment from "moment";

export default {
  name: "CostItemBinding",
  components: { SubMenuTree, ResizableModal },
  data() {
    return {
      allSelected: false,
      expenseYM: null,
      fileName: null,
      tableData: [],
      filteredData: [],
      checkedItems: [],
      treeOptions: {
        checkbox: false,
      },
      treeData: [],
      allTreeData: [],
      addExpenseTitle: "",
      disabledExpenseItems: [],
      loading: false,
      isBusy: false,
      costMonthlyStart: null,
      constMonthlyEnd: null,
      pointsMessage: [],
      rowCount: "",
      showModal: false,
      selectedTreeItem: {},
      selectedExpenseItem: "",
    };
  },
  computed: {
    /**
     * @description mapGetters to get the data from the store
     */
    ...mapGetters({
      selected_expense_item: "getSelectedExpenseItem",
      user_details: "getUserDetails",
      journal_import_data: "getJournalImportData",
    }),
    /**
     * @description fields used in the table for keys mapping
     */
    fields() {
      return [
        {
          key: "selectRow",
          label: "",
          class: "text-center normal-width",
        },
        {
          key: "cost_monthly",
          label: this.$t("EXPENSEYM"),
          class: "text-left normal-width",
        },
        {
          key: "deal_date",
          label: this.$t("TRADINGDATE"),
          class: "text-left normal-width text-nowrap",
        },
        {
          key: "accounts_name_nm",
          label: this.$t("ACCOUNT"),
          class: "text-left normal-width",
        },
        {
          key: "cost",
          label: this.$t("AMOUNT"),
          class: "text-center normal-width",
        },
        {
          key: "description",
          label: this.$t("DESCRIPTION"),
          class: "text-left date-normal-width",
        },
        {
          key: "expense_item_nm",
          label: this.$t("EXPENSEITEM"),
          class: "text-left normal-width",
        },

        {
          key: "filterElements",
          label: this.$t("BINDINGALREADYNOTSHOW"),
          class: "checkbox-header-width",
        },
      ];
    },
  },
  watch: {
    /**
     * @description to filter the data from the header checkbox
     */
    allSelected(value) {
      if (value) {
        setTimeout(() => {
          this.tableData = this._.filter(this.filteredData, (item) => {
            return item.expense_item_nm == null;
          });
        }, 0);
      } else {
        this.tableData = this.filteredData;
        this.checkedItems = [];
      }
    },
  },
  created() {
    this.getTreeData();
  },
  mounted() {
    this.file_no = this.journal_import_data.id; // get the journal file id from store
    this.rowCount = this.journal_import_data.rowCount
      ? this.journal_import_data.rowCount
      : "0";
    this.getJournalFileInfo();
    this.getTableData();
    this.$store.dispatch("updateSelectedExpenseItem", {
      accounts_name_cd: null,
      expense_item_cd: null,
      expense_item_nm: null,
    });
  },
  methods: {
    /**
     * @description function get the year and month from a given number
     * @name getDateMonth
     * @param dateInput
     * @returns string 'formatted date string'
     */
    getDateMonth(dateInput) {
      if (!dateInput) {
        return "";
      }
      return (
        String(dateInput).slice(0, 4) +
        this.$t("YEAR") +
        String(dateInput).slice(4, 6) +
        this.$t("MONTH")
      );
    },
    /**
     * @description function to get the journal file information
     * @name getJournalFileInfo
     * @param none
     * @returns nothing
     */
    async getJournalFileInfo() {
      this.checkedItems = [];
      const { data } = await BasicInfoApis.postAPI(
        endPoint.MASTERREADTJOURNALFILEINFO,
        {
          fieldlist: ["*"],
          conditions: [
            {
              key: "file_no",
              value: this.file_no,
            },
          ],
        }
      );
      if (data.success) {
        let responseData = data.data[0];
        // assign the response data to the each field
        this.expenseYM =
          this.getDateMonth(responseData.cost_monthly_start) +
          " ～ " +
          this.getDateMonth(responseData.cost_monthly_end);

        this.fileName = responseData.file_name;
        this.cost_monthly_start = responseData.cost_monthly_start;
        this.cost_monthly_end = responseData.cost_monthly_end;
        this.earnExcellentPoints();
      }
    },
    /**
     * @description function to diplay the expense items remaining to earn excellent points
     * @name earnExcellentPoints
     * @param none
     * @return nothing
     */

    async earnExcellentPoints() {
      const { data } = await BasicInfoApis.postAPI(
        endPoint.JOURNALEXPENSEPOINT,
        {
          client_no: this.user_details.client_account_no,
          cost_monthly_start: this.cost_monthly_start,
          cost_monthly_end: this.cost_monthly_end,
        }
      );
      if (data.success) {
        let responseData = data.data;
        // iterating each record to construct the formatted string and displaying to the user
        responseData.expenseItemMclientDetails.forEach((item) => {
          let count = this.calculatePoints(
            responseData.acquiredPoint.border_expense_items,
            item.max_rgist_expense_items
          );
          let cost_monthly = this.getDateMonth(item.cost_monthly);
          this.pointsMessage.push(
            cost_monthly +
              "はあと" +
              count +
              "費目登録するとエクセレントポイント獲得する。"
          );
        });
      }
    },
    /**
     * @description function to calculate the remaining expense item mapping to earn excellent point
     * @name calculatePoints
     * @param borderExpenseItems
     * @param maxRgistExpenseItems
     * @returns number 'formula will calculate the based on the params'
     */
    calculatePoints(borderExpenseItems, maxRgistExpenseItems) {
      return (
        borderExpenseItems + 1 - (maxRgistExpenseItems % borderExpenseItems)
      );
    },
    /**
     * @description function the get data of the journal file
     * @name getTableData
     * @param none
     * @return nothing
     */
    async getTableData() {
      this.checkedItems = [];
      this.isBusy = true;
      const { data } = await api.post(
        endPoint.MASTERJOINTJOURNALEXPENSEITEMLINKINFO,
        {
          limit: 50,
          offset: 0,
          paginate: false,
          fieldlist: [
            "t_journal_expense_item_link_info.link_no",
            "t_journal_expense_item_link_info.cost_monthly",
            "t_journal_expense_item_link_info.deal_date",
            "t_journal_expense_item_link_info.accounts_name_nm",
            "t_journal_expense_item_link_info.cost",
            "t_journal_expense_item_link_info.description",
            "m_expense_item.expense_item_nm",
            "t_journal_file_info.cost_monthly_start",
            "t_journal_file_info.cost_monthly_end",
            "t_journal_file_info.file_name",
            "m_expense_item.expense_item_cd",
          ],
          conditions: [
            {
              key: "t_journal_expense_item_link_info.file_no",
              value: this.file_no,
            },
          ],
          joins: [
            {
              table: "m_expense_item",
              join_type: "left",
              join_conditions: [
                {
                  join_using: "m_expense_item.expense_item_cd",
                  join_operator: "=",
                  join_with:
                    "t_journal_expense_item_link_info.link_expense_item_cd",
                },
                {
                  join_using: "m_expense_item.accounts_name_cd",
                  join_operator: "=",
                  join_with:
                    "t_journal_expense_item_link_info.link_accounts_name_cd",
                },
              ],
            },
            {
              table: "t_journal_file_info",
              join_type: "left",
              join_conditions: [
                {
                  join_using: "t_journal_file_info.file_no",
                  join_operator: "=",
                  join_with: "t_journal_expense_item_link_info.file_no",
                },
              ],
            },
          ],
          order_by: [
            {
              key: "t_journal_expense_item_link_info.link_no",
              value: "asc",
            },
          ],
        }
      );
      this.isBusy = false;
      if (data.success) {
        let responseData = data.data.data;
        this.tableData = responseData;
        this.filteredData = responseData;
        this.tableData.forEach((element, index) => {
          //this.$refs["checkbox" + index].checked = false; // initially checkbox is unchecked
          element.selectRow = false;
          element.deal_date = moment(element.deal_date).format("YYYY/MM/DD"); // format the date
        });
      } else {
        this.tableData = [];
      }
    },
    /**
     * @description function to show the expense item selection modal
     * @name showCostItemSelectModal
     * @param none
     * @return nothing
     */
    showCostItemSelectModal() {
      this.showModal = true;
      // this.$refs["costItemSelect-modal"].show();
    },
    /**
     * @description function to hide the expense item selection modal
     * @name applyCheckExpItem
     * @param none
     * @returns nothing
     */
    applyCheckExpItem() {
      this.$store.dispatch("updateSelectedExpenseItem", {
        accounts_name_cd: this.selectedTreeItem.accounts_name_cd,
        expense_item_cd: this.selectedTreeItem.expense_item_cd,
        expense_item_nm: this.selectedTreeItem.expense_item_nm,
      });

      this.tableData.forEach((element, index) => {
        if (element.selectRow == true) {
          this.tableData[index]["expense_item_nm"] = this.selectedExpenseItem;
        }
      });
      this.$refs.table.refresh();
      this.hideModal();

      // this.$refs["costItemSelect-modal"].hide();
    },
    hideModal() {
      this.showModal = false;
    },
    /**
     * @description function to toggle the selectAll checkbox in the table
     * @name toggleSelectAll
     * @param item
     * @returns nothing
     */
    toggleSelectAll(item) {
      item.item.selectRow = !item.item.selectRow;
      this.checkedItems = this.tableData.filter((checkedItem) => {
        if (checkedItem.selectRow == true) {
          return checkedItem;
        }
      });
    },
    /**
     * @description function to get the expense items from the master table
     * @name getTreeData
     * @param none
     * @return nothing
     */
    async getTreeData() {
      const { data } = await BasicInfoApis.postAPI(
        endPoint.READWITHJOINMEXPENSEITEM,
        {
          paginate: false,
          limit: 5000,
          fieldlist: [
            "m_accounts_name.accounts_name_cd",
            "m_accounts_name.accounts_name_nm",
            "m_expense_item.expense_item_cd",
            "m_expense_item.expense_item_nm",
          ],
          conditions: [
            {
              key: "m_accounts_name.use_flg",
              value: true,
            },
          ],
          joins: [
            {
              table: "m_accounts_name",
              join_type: "inner",
              join_conditions: [
                {
                  join_using: "m_accounts_name.accounts_name_cd",
                  join_operator: "=",
                  join_with: "m_expense_item.accounts_name_cd",
                },
              ],
            },
          ],
        }
      );
      if (data.success) {
        let responseData = data.data.data;
        this.allTreeData = responseData;
        let mapData = [];
        let groupedData = this._.groupBy(responseData, "accounts_name_nm");
        Object.keys(groupedData).forEach((key) => {
          let childrenArray = [];
          groupedData[key].forEach((item) => {
            let bindChildData = {
              text: item.expense_item_nm,
            };
            childrenArray.push(bindChildData);
          });
          let bindTreeData = {
            text: key,
            state: { expanded: true },
            children: childrenArray,
          };
          mapData.push(bindTreeData);
        });
        this.$store.dispatch("updateTree", mapData);
      }
    },
    /**
     * @description function to update the store once expense item selected
     * @name updateExpenseItem
     * @param item
     * @returns nothing
     */
    updateExpenseItem(item) {
      this.selectedExpenseItem = item;
      let treeItem = this._.find(this.allTreeData, { expense_item_nm: item });
      this.selectedTreeItem = treeItem;
    },
    /**
     * @description map the expense item to the corresponding journal file data to the db
     * @name mapExpenseItem
     * @param none
     * @returns nothing
     */
    async mapExpenseItem() {
      this.loading = true;
      const { data } = await BasicInfoApis.postAPI(
        endPoint.JOURNALLINKJOURNALEXPENSEITEM,
        {
          file_no: this.file_no,
          link_no: this._.map(this.checkedItems, "link_no"),
          link_expense_item_cd: this.selected_expense_item.expense_item_cd,
          link_accounts_name_cd: this.selected_expense_item.accounts_name_cd,
          update_id: localStorage.getItem("client_account_no"),
        }
      );
      if (data.success) {
        this.$swal({
          type: "success",
          text: "",
          timer: 5000,
        });
        this.getTableData();
      } else {
        this.$swal({
          type: "warning",
          text: this.$t("ERRORWHILELINKINGEXPENSEIEM"),
          timer: 5000,
        });
      }
      this.loading = false;
    },
  },
};
</script>

<style lang="scss" scoped>
.title-fs-max {
  font-size: 30px;
}

.hrBarType {
  height: 2px;
  border-width: 10;
  color: gray;
  background-color: gray;
}

