<template>
  <div id="subMenuTree" class="min-tree-height">
    <div class="font-weight-bold">{{ treeTitle }}</div>
    <template v-if="treeMapedData.length == 0">
      <div class="text-center errorColor">
        {{ $t("GENRALDATAERRORMESSAGE") }}
      </div>
    </template>
    <template v-else>
      <tree :data="treeMapedData" :options="treeOptions">
        <span slot-scope="{ node }" class="tree-text">
          <template v-if="!node.hasChildren()">
            <b-form-radio
              v-if="isParentDataSelected"
              v-model="selected"
              @change="onNodeSelected(node.text, node.parent.data.text)"
              :value="node.parent.data.text + node.text"
              :disabled="disabled(node.text)"
            >
              <u
                :class="{
                  disabled: disabled(node.text),
                  'selected-tree-text': selected == node.text,
                }"
                class="tree-text pointer"
              >
                {{ node.text }}
              </u>
            </b-form-radio>
            <b-form-radio
              v-else
              v-model="selected"
              @change="onNodeSelected(node.text)"
              :value="node.text"
              :disabled="disabled(node.text)"
            >
              <u
                :class="{
                  disabled: disabled(node.text),
                  'selected-tree-text': selected == node.text,
                }"
                class="tree-text pointer"
              >
                {{ node.text }}
              </u>
            </b-form-radio>
          </template>
          <template v-else>
            <u>{{ node.text }}</u>
          </template>
        </span>
      </tree>
    </template>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  name: "SubMenuTree",
  components: {},
  inheritAttrs: false,
  props: {
    treeOptions: {
      type: [Object],
      default: () => {},
    },
    disabledItems: {
      type: [Array],
      default: () => [],
    },
    treeTitle: {
      type: String,
      default: () => [],
    },
    industry: {
      type: Boolean,
      default: false,
    },
    reductionProposal: {
      type: Boolean,
      default: false,
    },
    isParentDataSelected: {
      type: Boolean,
      default: false,
    },
  },
  data: () => {
    return {
      selected: "",
    };
  },
  computed: {
    ...mapGetters({
      treeData: "getTree",
      industryTreeData: "getIndustryTree",
      getProposalTreeData: "getReductionProposalTree",
      selected_expense_item: "getSelectedExpenseItem",
    }),
    treeMapedData() {
      if (this.industry) {
        return this.industryTreeData;
      }
      if (this.reductionProposal) {
        return this.getProposalTreeData;
      }
      return this.treeData;
    },
  },
  mounted() {
    this.selected = this.selected_expense_item.expense_item_nm;
  },
  created() {},
  methods: {
    disabled(text) {
      return this.disabledItems.indexOf(text) == -1 ? false : true;
    },
    onNodeSelected(item, parentItem) {
      if (this.disabledItems.indexOf(item)) {
        this.$emit("update-expense-type", item, parentItem);
      }
    },
  },
};
</script>
<style scoped lang="scss">
$primary: #062854 !default;
.tree-text,
.link-text {
  font-size: 0.7rem;
  font-weight: 800;
  color: $primary;
}
</style>
<style lang="scss">
#subMenuTree .tree-content {
  padding-top: 0px !important;
  padding-bottom: 0px !important;
  padding-left: 0px !important;
}
.disabled {
  opacity: 0.4;
}

.tree-checkbox.checked,
.tree-checkbox.indeterminate {
  background-color: #fff !important;
  border-color: #000 !important;
}

.tree-checkbox {
  border-color: #000 !important;
  height: 23px !important;
  width: 23px !important;
}

.tree-checkbox.checked:after {
  border: 1.5px solid #000 !important;
  border-left: 0 !important;
  border-top: 0 !important;
  left: 8px !important;
  top: 2px !important;
  height: 12px !important;
  width: 5px !important;
}
</style>
