<template>
  <div>
    <modal
      id="vue-modal"
      ref="vue-modal"
      :draggable="draggable"
      :adaptive="true"
      :resizable="resizable"
      @closed="hide()"
      name="vue-modal"
      width="500"
    >
      <div class="vue-modal-header">
        <div class="title-fw title-fs line-height text-dark ml-2 float-left">
          {{ modalTitle }}
        </div>
        <div class="d-flex justify-content-end">
          <b-button
            @click="hide()"
            variant="primary"
            class="back-btn close-btn mt-2"
          >
            {{ $t("CLOSE") }}
          </b-button>
        </div>
      </div>
      <div class="vue-modal-content">
        <slot name="modal-content"></slot>
      </div>
      <div class="vue-modal-footer">
        <div class="w-100 mr-3"></div>
        <div class="d-flex justify-content-end mr-3">
          <b-button
            @click="submitModal"
            variant="primary"
            type="submit"
            class="px-4 send-btn float-right back-btn"
            >{{ $t("SELECTEXPENSEITEMS") }}</b-button
          >
        </div>
      </div>
    </modal>
  </div>
</template>

<script>
export default {
  name: "ResizableModal",

  props: {
    modalTitle: {
      type: String,
      default: () => [],
    },
    resizable: {
      type: Boolean,
      default: false,
    },
    draggable: {
      type: Boolean,
      default: true,
    },
    showModal: {
      required: true,
      type: Boolean,
      default: true,
    },
  },
  data() {
    return {};
  },
  watch: {
    showModal(value) {
      if (value) {
        this.show();
      } else {
        this.hide();
      }
    },
  },
  methods: {
    show() {
      this.$modal.show("vue-modal");
    },
    hide() {
      this.$emit("modal-hide");
      this.$modal.hide("vue-modal");
    },
    submitModal() {
      this.$emit("modal-hide");
      this.$emit("modal-submit");
    },
  },
};
</script>

<style scoped lang="scss">
// #costItemSelect-modal .card-body {
//   max-height: 500px;
//   overflow: auto;
// }

#vue-modal {
  z-index: 2060;
  height: 500px;
}
.vue-modal-header {
  margin: 0.5rem 0.5rem 0rem 0.5rem;
}
.vue-modal-content {
  margin: 1rem;
}
.close-btn {
  margin-right: 0.5rem !important;
}
.pop-up-box {
  max-height: 250px !important;
  min-height: 250px !important;
  height: 250px;
  overflow: auto;
}
.line-height {
  line-height: 3rem;
}
</style>
<style lang="scss">
.v--modal {
  height: 375px !important;
  z-index: 1050;
}
@media only screen and (max-width: 768px) {
  .v--modal {
    width: 100% !important;
    left: 0px !important;
  }
}
.vm--modal {
  height: 375px !important;
  z-index: 1050;
}
@media only screen and (max-width: 768px) {
  .vm--modal {
    width: 100% !important;
    left: 0px !important;
  }
}
</style>
